/* Button Styles */
.btn {
    color: #fff;
    font-size: 13px;
    letter-spacing: 0.34px;
    text-transform: uppercase;
    font-weight: 500;
    border: 0;
    border-radius: 5px;
    padding: 11px 20px;
    transition: all 0.3s ease;
    outline: none !important; 
    box-shadow: none !important;
}
.btn-sm {
    color: #fff;
    font-size: 12px;
    letter-spacing: 0.34px;
    text-transform: uppercase;
    font-weight: 500;
    border: 0;
    border-radius: 5px;
    padding: 8px 15px;
    transition: all 0.3s ease;
    outline: none !important;
    box-shadow: none !important;
}

.btn-hollow {
    border: 1px solid #fff;
    color: #fff;
    &:focus,
    &:link,
    &:visited {
        border: 1px solid #fff;
        color: #fff;
    }
    &:hover,
    &:active {
        opacity: 0.7;
    }
}

.btn-default, .btn-default:link, .btn-default:visited, .btn-default:focus {
    background: @action_color;
    color: #ffffff;
    white-space: nowrap;
}

.btn-default:hover, .btn-default:active {
    background: lighten(@action_color, 3%);
    color: #fff;
}

.btn-primary, .btn-primary:link, .btn-primary:visited, .btn-primary:focus {
    background-color: @action_secondary;
    color: #fff;
}
.btn-primary:hover, .btn-primary:active {
    background-color: lighten(@action_secondary, 3%);
    color: #fff;
}

.btn-info, .btn-info:link, .btn-info:visited, .btn-info:focus {
    background-color: @primary_color;
    color: #fff;
}
.btn-info:hover, .btn-info:active {
    background-color: lighten(@primary_color, 3%);
    color: #fff;
}
.btn:hover .glyphicon {
    transform: scale(1.25);
}

.btn-danger, .btn-danger:link, .btn-danger:visited, .btn-danger:focus { background: #c9302c; color: #fff; }

.btn-set { width: 240px; }

.btn-default.disabled, .btn-default[disabled], .btn-default.disabled:hover, .btn-default[disabled]:hover, .btn-default.disabled:focus, .btn-default[disabled]:focus, .btn-default.disabled:active, .btn-default[disabled]:active, .btn-default.disabled.active, .btn-default[disabled].active {
    background: #999999;
}

.bkg-primary { background: @primary_color; }
.bkg-action { background: @action_color; }

.background-wave-green {
    background-size: cover;
    width: 100%;
    background: @action_color;
}

/* Alert Styling */

.alert-floater-wrapper .alert{
    border:2px solid #fff;
    background-color:#E2ECF8;
    background-image: url(data:image/png;base64,);
    font-size: 1.1em;
    color: #333;
    margin-bottom: 0;
}

.alert-floater-wrapper{
    width: 100%; 
    max-width: 100%;
    transform: translateX(-50%);
    left: 50%;
    z-index: 20009;
    border-radius:10px;
    position:fixed;
    top:-100%;
    -webkit-transition:top 0.7s ease-out;
    -moz-transition:top 0.7s ease-out;
    transition:top 0.7s ease-out;
    z-index: 26000;
    border: 3px solid #fff;
    background-color:#4CABEB;
    box-shadow: 0px 4px 7px 0px rgba(0,0,0, 0.2);
    padding:10px;
    @media (min-width: 768px) and (max-width: 992px) { width: 100% !important; }
    @media (max-width: 768px){ margin: 0 2%; width: 96% !important; }
}

.alert-icon{
    width:60px;
    height:50px;
    background-image: url(../images/icons_alert.png);
    margin-right:15px;
    position: absolute;
    top: 50%;
    left: 20px;
    margin-top: -25px;
}

.alert-body{
    padding: 0 20px 0 70px;
    text-align:center;
    @media(max-width: @screen-xs-max){
      font-size: 12px;
      padding: 0 0 0 0;
    }
}

.alert-body ul, .alert-body ol{
    list-style-position: inside;
    list-style-type: square;
    padding: 0;
    color: #666;
}

.alert-close{
    position:absolute;
    top:19px;
    right:18px;  
    background-image: url(../images/icons_alert.png);
    background-position:0 -100px;
    width:12px;
    height:12px;
    opacity:0.5;
    cursor:pointer;
}

.alert-error .alert-close{
    background-position:-12px -100px;
}

.alert-close:hover{
    opacity:1;
}

.alert-wrapper.alert-error .alert-icon {
    background-position:0 -50px;
}

.alert-floater-wrapper.active{top:10px;}

.alert-wrapper.alert-error{
    border-color: white;
    background-color: #FC594B;
}

.alert-wrapper.alert-error .alert{
    background-color:#FFEFE0;
    background-repeat: repeat;
}

.alert-info a { color: @action_color; }


.flex-minheight100 { min-height: 100px; }
.flex-minheight200 { min-height: 200px; }
