/* Lists - such as candidates, jobs */
.list-item-wrapper {
    padding: 11px 15px;
    display:block;
}

.list-item-wrapper.odd {
    background: #f3f3f3;
    border-top: 2px solid #f2f2f2;
    border-bottom: 2px solid #f2f2f2;
}
.list-item-wrapper.even {
    background: #f9f9f9;
    border-top: 2px solid #f9f9f9;
    border-bottom: 2px solid #f9f9f9;
}

a.list-item-wrapper:link, a.list-item-wrapper:visited, a.list-item-wrapper:active {
    color:#484848;
}
a.list-item-wrapper:hover .text-subtext { color: @primary_color; }

a.list-item-wrapper:hover {
    background-color: #D4E9F7;
    border-top-color: #fff;
    border-bottom-color: #fff;
}

.list-header {
    background:@primary_color;
    color:#eaeaea;
    font-size:1.3em;
    padding:15px;
    margin-bottom:5px;
    line-height: 1.2em;
}

.list-item-title { color: @primary_color; }

.list-divider {
    @media(max-width: @screen-xs-max){
        border-bottom: 1px solid #ddd;
        display: inline-block;
        margin-bottom: 5px;
        padding-bottom: 5px;
    }
}

/* Quotes */
.quote-outer {
    height: 100%;
    width: 940px;
    margin: 60px auto 0 auto;
    position:relative;
    display: table;
    @media (max-width: 1024px) {
        width: 95%;
    }
    @media(max-width: @screen-xs-max) {
        margin-top: 5px;
        font-size: 12px; line-height: 22px;
    }
}
.quote-container {
    width: 33.333%;
    display: table-cell;
    height: 100%;
    @media(max-width: @screen-xs-max){
        width: 100%;
        display: block;
        padding: 0;
        margin-bottom: 15px;
        height: auto;
    }
}
.quote-content {
    padding: 27px 20px 75px 20px;  
    background: #fff;
    border-radius: 5px;
    min-height: 281px;
    position: relative;
    height: 100%;
    transition: all 0.3s ease; 
    @media(max-width: @screen-xs-max){
        min-height: 0;
        padding: 27px 20px;
        height: auto;
    }
    &:after {
        content: '';
        width: 22px;
        height: 22px;
        position: absolute;
        background: #fff;
        bottom: -9px;
        left: 50%;
        transform: translateX(-50%) rotate(45deg);
        @media(max-width: @screen-xs-max) {
            display: none;
        }
    }
    &:hover {
        transform: scale(1.045);
        @media(max-width: @screen-xs-max) {
            transform: none;
        }
    }
    &.tech-event {
        min-height: 215px;
        @media(max-width: @screen-xs-max){
            min-height: 0;
        }
    }
}
.quote-name {
    font-size: 15px;
    font-weight: 700;
    letter-spacing: 0.28px;
    line-height: 20px;
    position: absolute;
    bottom: 30px;
    @media(max-width: @screen-xs-max){
        position: relative;
        bottom: 0;
    }
}
.quote-image {
    margin: 25px auto 0 auto;
    display: block;
    border-radius: 100px;
    border: 5px solid #fff;
    filter: grayscale(100%);
    box-shadow: 1px 1px 2px 0px rgba(0, 0, 0, 0.12);
    &-sm {
        width: 33.333%;
        display: table-cell;
    }
    @media(max-width: @screen-xs-max) {
        width: 53px;
        position: absolute;
        right: 13px;
        bottom: 10px;
        border: 0;
    }
}

/* Tabbed links */

.tab-item {
    background: #f3f3f3;
    background: -moz-linear-gradient(top,  #ffffff 0%, #f4f4f4 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#ffffff), color-stop(100%,#f4f4f4));
    background: -webkit-linear-gradient(top,  #ffffff 0%,#f4f4f4 100%);
    background: -o-linear-gradient(top,  #ffffff 0%,#f4f4f4 100%);
    background: -ms-linear-gradient(top,  #ffffff 0%,#f4f4f4 100%);
    background: linear-gradient(to bottom,  #ffffff 0%,#f4f4f4 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#f4f4f4',GradientType=0 );
    border: 1px solid #c8c8c8;
    border-radius: 5px;
    display: inline-block;
    padding: 8px;
    z-index: 10;
    color: #999999;
    font-weight: 700;
    text-align:center;
    text-transform: uppercase;
    margin: 0 0 0 10px;
    cursor: pointer;
    @media(max-width: 900px){
        font-size: 14px; 
        padding: 5px;
    }
    @media(max-width: @screen-xs-max){
        margin: 0 0 10px 5px;
        font-size: 12px;
    }
}

a.tab-item.active,  a.tab-item.active:link, a.tab-item.active:visited { color: #999999; }

a.tab-item,  a.tab-item:link, a.tab-item:visited, a.tab-item:hover {
    background-color: #DBE7F0;
