@charset "UTF-8";
/* CSS Document */

body { 
	font-size: 14px;
    line-height: 22px;
    letter-spacing: 0;
	font-family: @default_font;
    position:relative;
    color: @default_color;
    background: #f1f1f1;
    overflow-x: hidden;
}

a:link, a:visited, a:active{ text-decoration:none; color: @secondary_color;}
a:hover{ color:#000;}

strong{ font-weight:bold;}
em { font-style:italic;}

h1, h2, h3, h4, h5, h6 {
	font-family: @alt_font;
	color: @primary_color;
	font-weight:bold;
	margin-top: 0;
	line-height:1em;
}

h1 {
	font-size: 41px;
	font-weight: bold;
	letter-spacing: -2.07px;
}
h2 {
	font-size: 28px;
	font-weight: 500; 
	letter-spacing: 0.04px;
	margin-bottom: 21px;
	color: @default_color;
}
h3 {
	font-size: 25px;
	font-weight: 600; 
	letter-spacing: -1.07px;
	color: #68ADE9;
}
h4 {
	font-size: 24px;
	font-weight: 600; 
	letter-spacing: -0.63px;
	color: @default_color;
	margin-bottom: 19px;
}

/* apply a natural box layout model to all elements */
*, *:before, *:after {
  	-moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box;
 }

/* Hide visually and from screenreaders, but maintain layout */
.invisible { visibility: hidden; }


.clearfix:after {
  content: "";
  display: table;
  clear: both;
}
	.hidden-tablet {
		display: none; 
	}


/* Button Styles */
a.btn,
.btn-submit {
    color: #fff;
    font-size: 15px;
	font-weight: bold;
    font-family: @alt_font;
    letter-spacing: 0;
    transition: all 0.3s ease;
    background: @secondary_color;
    border-radius: 5px;
    text-transform: uppercase;
    display: inline-block;
    padding: 16px 25px;
    white-space: normal;
    &:hover,
    &:active,
    &:focus {
        background: lighten(@secondary_color, 3%);
        color: #fff;
    }
}
a.btn-white {
    color: @default_color;
    background: #fff;
    &:hover,
    &:active,
    &:focus {
    	background: #fff;
        color: @secondary_color;
    }
}
a.btn-blue {
    color: #fff;
    background: @primary_color;
    &:hover,
    &:active,
    &:focus {
    	background: lighten(@primary_color, 3%);
        color: #fff;
    }
}
a.btn-sm {
    padding: 8px 15px; 
    font-size: 13px; 
}
a.btn-arrow {
	padding: 11px 32px 8px 15px;
	font-weight: bold;
	&:after {
		content: '>';
	    margin-left: 10px;
	    position: absolute;
	    margin-top: 0;
	}
}
.flex_button {
	color: #fff;
    font-family: @alt_font;
    letter-spacing: 0.9px;
    font-weight: 500; 
    transition: all 0.3s ease;
    background: @secondary_color;
    border-radius: 5px;
    text-transform: uppercase;
    display: inline-block;
    padding: 8px 15px; 
    font-size: 13px; 
    cursor: pointer;
    margin-left: 10px;
    &:hover,
    &:active,
    &:focus {
        background: lighten(@secondary_color, 3%);
        color: #fff;
    }
}

.btn-pop, .btn-pop:visited, .btn-pop:active {
	border-bottom:4px solid #024d80;
	border-radius:3px;
}
.btn-pop:hover {
	margin-top:4px;
	margin-bottom:0;
	border-bottom:none;
}

.btn-info, .btn-info:focus, .btn-info:active {
	color: #555555;
	background: #def3fb;
	background: -moz-linear-gradient(top,  #def3fb 0%, #def3fb 52%, #b6e0fa 52%, #b6e0fa 100%);
	background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#def3fb), color-stop(52%,#def3fb), color-stop(52%,#b6e0fa), color-stop(100%,#b6e0fa));
	background: -webkit-linear-gradient(top,  #def3fb 0%,#def3fb 52%,#b6e0fa 52%,#b6e0fa 100%);
	background: -o-linear-gradient(top,  #def3fb 0%,#def3fb 52%,#b6e0fa 52%,#b6e0fa 100%);
	background: -ms-linear-gradient(top,  #def3fb 0%,#def3fb 52%,#b6e0fa 52%,#b6e0fa 100%);
	background: linear-gradient(to bottom,  #def3fb 0%,#def3fb 52%,#b6e0fa 52%,#b6e0fa 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#def3fb', endColorstr='#b6e0fa',GradientType=0 );
}
a.btn-info, .btn-info a, a.btn-info:hover, .btn-info a:hover, .btn-info:link, a.btn-info:link { color: #555555 !important; }
.btn-info:hover {
	background: #ffffff;
}
.overlay-title {
	font-size: 12px;
    text-transform: uppercase;
    letter-spacing: 0;
    margin-bottom: 20px;
    display: inline-block;
    background: #e8e8e8;
    padding: 7px 9px 7px 30px;
