.jobseeker_profile_section_replaceimage{
	position:absolute;
	z-index:1;
	left:5px;
	background:#fafafa;
	border: 2px solid #aaa;
	box-shadow: 1px 1px 2px 1px rgba(100,100,100,0.1);
	padding:15px;
	margin-top:-20px;
	min-width:420px;
}

.jobseeker_profile_section_replaceimage:after{
	border-color: transparent transparent #fafafa transparent;
	border-style:solid;
	border-width:20px;
	width:0;
	height:0;
	content:'';
	position:absolute;
	top:-40px;
	left:48px;
}

.jobseeker_profile_section_replaceimage:before{
	border-color: transparent transparent #aaa transparent;
	border-style: solid;
	border-width: 22px;
	width: 0;
	height: 0;
	content: '';
	position: absolute;
	top: -45px;
	left: 46px;
}

